import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

const Navbar = ({ openSideBar, sideBarState, handleLogout, toggle }) => (
  <Fragment>
    <nav className="navbar navbar-expand-lg navbar-bg-color navbar-lg navbar-dark">
      <button className="btn btn-menu mr-2" onClick={() => openSideBar(!sideBarState)}>
        <i className="fas fa-bars" />
      </button>
      <Link to="/" className="navbar-brand">
        Habit Tracker
      </Link>
      <div className="ml-auto">
        <ul className="navbar-nav">
          <li className="nav-item">
            <a className="nav-link" href="" onClick={() => handleLogout()}>
              Logout
            </a>
          </li>
        </ul>
      </div>
      <button className="btn btn-secondary ml-2" onClick={() => toggle()}>
        <i className="fas fa-lightbulb" />
      </button>
    </nav>
  </Fragment>
);

export default Navbar;



